
import Scroll from '../../../base/scroll/scroll'
import Info from '../../../base/info/info'
import Upload from '../../../base/upload/upload'
import MButton from '../../../base/button/button'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV2Data from 'vux/src/datas/china_address_v2.json'
import value2name from 'vux/src/filters/value2name.js'

export default {
name: "yinHangKa",
components: {
Scroll,
XInput,
Group,
Info,
MButton,
Upload,
XAddress
},
data() {
return {
infos: [
'',
'',
''
],
btnSaveState: false,
title:'',
addressValue:[],
addressValueString:'',
addressData: ChinaAddressV2Data,
userName:'',
userAccount:null,
userBsName:'',
userAccountType:(val)=>{
if(val.length>=15&&val.length<=17){
return {
valid : true
}
}
return {
valid: false,
msg: ''
}
}
}
},
watch:{
addressValue(value){
this.addressValueString = value2name(value, ChinaAddressV2Data)
this.checkState()
},
userName(){
this.checkState()
},
userBsName(){
this.checkState()
},
userAccount(){
this.checkState()
}
},
methods: {
checkState(){
let isUserName = this.$refs.userName.valid
let isAccount = this.$refs.userAccount.valid
let isAddressValueString = this.addressValueString.length >0
let isUserBsName = this.userBsName.length >0
if(isUserName&&isAccount&&isAddressValueString&&isUserBsName){
this.btnSaveState = true
return false
}
this.btnSaveState = false
},

apply() {
console.log(123)

if (this.btnSaveState) {
console.log('')
}
}
}
}



